@import url('https://fonts.googleapis.com/css2?family=Goldman:wght@400;700&display=swap');

*{
    font-family: 'Goldman', cursive;
}
.hero-wrapper{
    margin-top: 80px;
}
.hero{
    position: relative;
    min-height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    /* border: 1px solid black; */
    border-radius: 15px;
    padding: 20px 40px;
    margin: 5px 0;
    overflow: hidden;
    z-index: 1;
}

.title{
    text-align: center;
    font-size: 25px;
    font-weight: 700;
    text-transform: capitalize;
    z-index: 2;
}
.body{
    line-height: 200%;
    z-index: 2; 
}

.comments{
    z-index:2;
    border-top: 1px dashed black;
}
.button__back{
    display: block;
    padding: 5px 10px;
    border: none;
    border-radius: 6px;
    background-color: rgb(131, 131, 131);
    color: white;
    font-size: 25px;
    font-weight: 700;
    transition: opacity 0.2s;
    cursor: pointer;
    margin: 5px
}
.button__back:hover{
    opacity:0.8;
    
}

.background-image{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-position: center;
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: cover; /* Resize the background image to cover the entire container */
    z-index: -1;
}

.overlay{
    position: absolute;
    left: 0;
    top: 0;
    background-color: rgba(255, 255, 255, 0.5);
    width: 100%;
    height: 100%;
    z-index: 1;
}
.prev{
    position: absolute;
    left: 0;
    top: 0;
    width: 35px;
    height: 100%;
    z-index: 4;
    background-color: white;
    background-color: transparent;
    border:none;
    cursor: pointer;
    outline: none;
    transition:  opacity background-color 0.2s;
}
.prev:hover{
     opacity: 0.2;
    background-color: rgb(163, 163, 163);
}
.next{
    position: absolute;
    right: 0;
    top: 0;
    width: 35px;
    height: 100%;
    z-index: 4;
    background-color: white;
    background-color: transparent;
    border:none;
    cursor: pointer;
    outline: none;
    transition:  opacity background-color 0.2s;
}
.next:hover{
    opacity: 0.2;
   background-color: rgb(163, 163, 163);
}
.arrow {
    position: absolute;
    width: 30px;
    height: 30px;
    top: 50%;
   
    margin-top: -15px;
    border: 3px solid #c5c5c5;
    border-right: none;
    border-bottom: none;
    z-index: 2;
    cursor: pointer;
    background-color: transparent;
    transition: opacity 0.2s;
}

.arrow-prev {
    transform: rotate(-45deg);
    left: 10px;
}

.arrow-next {
    transform: rotate(-225deg);
    right: 10px;
}